.app-version {
  display: inline-block;
  padding: 4px 8px;
  font-size: 0.75rem;
  color: #666;
  background-color: #f5f5f5;
  border-radius: 4px;
  font-family: monospace;

  .version-info {
    display: flex;
    align-items: center;
    gap: 8px;
  }

  .version-text {
    font-weight: 500;
  }

  .mode-badge {
    display: inline-flex;
    align-items: center;
    font-size: 0.7rem;
    padding: 2px 6px;
    border-radius: 3px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    cursor: help;

    &.online {
      background-color: #e8f5e9;
      color: #2e7d32;
    }

    &.offline {
      background-color: #fff3e0;
      color: #e65100;
    }
  }
}
